<template>
  <div id="about">
    <div class="home-title">
      <h2><Icon type="md-person" />关于博主</h2>
    </div>
    <div class="me">
      <div class="infos" v-for="(val, index) in about">
        <h2>
          <Icon v-if="index===0" type="ios-contact" color="#0097ee"/>
          <Icon  v-else type="md-list-box" color="#0097ee"/>
          {{val.head}}
        </h2>
        <ul>
          <li v-for="text in val.text_li" v-text="text"></li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
    export default {
      name: "aboutme",
      data(){
          return{
            about:[]
          }
      },
      mounted(){
        this.getConfig()
      },
      methods:{
        getConfig(){
          this.$http.get("../static/config/about.json",{}).then(res => {
            this.about = res.data.infos;
          },error=>{
            this.$Notice.error({
              title: '错误信息',
              desc: 'get about.json file error!'
            });
          })
        }
      }
    }
</script>

<style scoped lang="less">
  /*start less类*/
  .xflex(@num){
    flex: @num;
    -webkit-flex: @num;
    -ms-flex: @num;
    -moz-box-flex: @num;
  }
  .xdisplayflex(){
    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex
  }
  .xboxshow(){
    -moz-box-shadow:1px 1px 5px #515151;
    -webkit-box-shadow:1px 1px 5px #515151;
    box-shadow:1px 1px 5px #515151;
  }
  .bgimg(@path){
    background-image: url(@path);
    -moz-background-size:cover; /* 老版本的 Firefox */
    background-repeat:no-repeat;
    background-size: cover;
    background-position: center center;
  }
  .h2(){
    h2{
      border-bottom: 1px solid rgba(83, 83, 83, 0.15);
    }
    h2::after{
      content: "";
      display: block;
      width: 20%;
      border: 1px solid rgb(0, 162, 255);
      vertical-align: middle;
    }
  }
  @color:rgb(102, 102, 102);
  .active{
    color: #00a2ff !important;
    font-weight: 700;
  }
  /*end less类*/
  @media (max-width: 750px){
    #about{
      width: 90%;
      margin: 0 auto;
      .home-title{
        text-align: left;
        .h2();
      }
      .me{
        margin: 20/32rem 20/32rem;
        color: @color;
        .infos{
          margin: 20/32rem 0;
          text-align: left;
          border: 1/32rem solid rgba(102, 102, 102, 0.5);
          border-radius: 5/32rem;
          .xboxshow();
          .h2();
          ul{
            margin: 0 8/32rem ;
            li{
              margin: 10/32rem 0;
              font-size: 12/32rem;
            }
          }
        }
      }
    }
  }
  @media (min-width: 750px){
    #about{
      .home-title{
        text-align: left;
        .h2();
      }
      .me{
        margin: 20px 20px;
        color: @color;
        .infos{
          margin: 20px 0;
          text-align: left;
          border: 1px solid rgba(102, 102, 102, 0.5);
          border-radius: 5px;
          .xboxshow();
          .h2();
          ul{
            margin: 0 8px ;
            li{
              margin: 10px 0;
              font-size: 16px;
            }
          }
        }
      }
    }
  }
</style>



